.all-star {
  display: flex;
  .stars {
    height: 15px;
    width: 15px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    &:nth-child(-n + 4) {
      background-image: url(../images/star-active.svg);
    }
  }
}

.feedback__coment {
  font-size: 11px;
}
